import { Divider, Image, Card } from "semantic-ui-react";

import Masonry from "react-masonry-css";

const modSeq = (a, b) => {
  return (a % b) + 1;
};

const MessageItems = ({ data, size }) => {
  const breakpoints = {
    default: 3,
    1100: 2,
    700: 1,
  };
  return (
    <Masonry
      breakpointCols={breakpoints}
      className="my-masonry-grid"
      columnClassName="my-masonry-grid_column"
    >
      {data.map(({ message, user, image }, i) => (
        <div className={"message_card"} data-aos="fade-up">
          <Image
            className={"message_card_icon"}
            src={`/icon/icon_${modSeq(i, 7)}.png`}
          />
          <Card key={i} centered>
            {image && <Image src={image} wrapped ui={false} />}
            <Card.Content>
              <Card.Description>{message}</Card.Description>
              <Divider />
              <Card.Header>{user}</Card.Header>
            </Card.Content>
          </Card>
        </div>
      ))}
    </Masonry>
  );
};

export default MessageItems;
